import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY, fetchPostH, getUrlServerSide } from "../../../stores/Utils";



import "../component/dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import SignatureBox from "../component/SignatureBox";
import { generatePD, fillInformationBiometricAppointment } from "../../BirthCerticate/services";
import AddAddress from "./AddressAdd";
import { SvgIcon_Cmpt } from "../../../components";
import UpdAddress from "./AddressUpdate";
import DeleteAlert from "../component/deleteAlert";




const Svg = SvgIcon_Cmpt();




const AddressDashboard  = ({ openUpd,   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);


	const updObs =  ()=>{
		set_obs(gen16CodeId())
	}
	

	const addAddress = async (v)=>{
		let ffm = {...getStore("upd_client")};

		let residences = ffm?.residences || [];
		residences.push(v);
		

		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {residences }
		};

		// console.log(bdyq2)
		
		/*  */
		const res = fetchPostH(getUrlServerSide(), bdyq2);
	
		const td = await res;
		if (td && td.data) {
			ffm.residences = residences;
			updStore("upd_client", ffm);
			updStore("new_address", null);
			updObs()
		}
		
	}


	
	const updAddress = async (v, indX2)=>{
		let ffm = {...getStore("upd_client")};

		let residences = ffm?.residences || [];
		residences[indX2] = v;


		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {residences }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.residences = residences;
			updStore("upd_client", ffm);
			updStore("upd_address", null);
			updObs()
		}
		
	}



	const openUpdView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddAddress confirm={addAddress} />
		OpenModal(modal_data);
	}

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			// let user = {...getStore("upd_client")};
			set_obs(gen16CodeId())
		}
	});


	
	
	const handleSignature =  (v) => {
		updStore("b64_signature",v);
	}




	

	const printIBA  = async ()  => {

		let user = {...getStore("upd_client")};
		let dob = (new Date(user?.dateOfBirth));
		let MM = dob.getMonth()+1;
		let frm_userInfo = {
			dobMonth: _monthNames["en"][MM],
			dobYear:dob.getFullYear(),
			dobDay: dob.getDate(),
			firstName:user?.firstName,
			middleName:user?.middleName?user?.middleName:"",
			lastName: user?.lastName,
			height: user?.height,
			weight: user?.weight,
		}
		let pdfUri = await fillInformationBiometricAppointment(frm_userInfo, true, true);
		window.open(pdfUri);
	}


	let residences = getStore("upd_client")?.residences || [];




	return (
		<div className=" ">
			<div className={"detailClient"}>
				<div className="_dsplFlx ">
					<p className="form_title">
						Direcciones
					</p>
					<div className="flexSpace"/>
					<p className="group_title addBtn" onClick={openUpdView}>
						Agregar Direccion
					</p>
				</div>
			</div>
			<div className=" ">
				{residences.map((residence, indX2)=> 
					<AddressItem  
						residence={residence} 
						key={indX2}
						index={indX2}
						updAddress={updAddress}
					/>
				)}
			</div>
		</div>
	);
};

export default AddressDashboard;







const AddressItem  = ({ residence,   updAddress, index }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const updObs  =  (v)  => {
		set_obs( gen16CodeId());
	};

	const copyToClpb  =  (v)  => {
		copyToClipboard( v,updObs);
	};

	const _updAddress  =  (v)  => {
		updAddress(v,index)
	};

	const openUpdView =  () => {

		updStore("upd_address", residence);
		updStore("upd_address_bck", JSON.parse(JSON.stringify(residence)));
		

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<UpdAddress confirm={_updAddress} />
		OpenModal(modal_data);
	}


	const confirmDelete =  (v)  => {
		// 
	};



	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert 
				confirm={confirmDelete}
				lbl_p={"Eliminar Direccion"} 
				lbl_span={"Una vez eliminado no podra ser recuperado"}
			/>
			OpenModal(modaldata);
		}
	}


	return (

		<div className="_dsplFlx ">
			<div className="flexSpace"/>
			<div className={`card_address modern `}  >
				<div className="_dsplFlx ">
					<div className="title_card" onClick={()=>copyToClpb(residence.addressLineOne)}>
						{residence.addressLineOne}
					</div>
					<div className="title_card" onClick={()=>copyToClpb(residence.addressLineTwo)}>
						{residence.addressLineTwo}
					</div>
					<div className="flexSpace"/>
					<div className={"icon_action _dsplFlx"}>
						<div className={"menu_icon _dsplFlx"}> 
							<div className="flexSpace"/>
							<Svg 
							name={"more_vert"}	/>
						</div>
						<div className={"menu_icon_acc _dsplFlx"}>
							<div 	onClick={openUpdView}>
								<Svg name={"outline_edit"}	/>
							</div>
							<div className="flexSpace"/>
							<div 	onClick={OpenDeleteAlert}>
								<Svg name={"outline_delete"}	/>
							</div>
						</div>
					</div>
				</div>
				<div className="body_card">
					<div className="_dsplFlx city_state">
						<div className="h4_gsans"  onClick={()=>copyToClpb(residence.city)}>
							{residence.city}
						</div>
						<div className="h4_gsans" onClick={()=>copyToClpb(residence.state)}>
							{residence.state}
						</div>
						<div className="h4_gsans" onClick={()=>copyToClpb(residence.zipcode)}>
							{residence.zipcode}
						</div>
					</div>
				</div>
				<div className="">
					<div className="_dsplFlx city_state">
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence.country)}>
							{residence.country}
						</div>
						<div className="flexSpace"/>	
				
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.fromDate?.month)}>
							{residence?.fromDate?.month}
						</div>
						<div className="h5_gsans" >
							{"/"}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.fromDate?.year)}>
							{residence?.fromDate?.year}
						</div>
						<div className="h5_gsans" >
							{" - "}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.toDate?.month)}>
							{residence?.toDate?.month}
						</div>
						<div className="h5_gsans" >
							{"/"}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.toDate?.year)}>
							{residence?.toDate?.year}
						</div>
					</div>
				</div>
			</div>
			<div className="flexSpace"/>
		</div>
	)
}











const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }




  

const TextView  = ({ init, label, width, value2copy }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);


	const updObs = ()=>{
		set_obs(gen16CodeId())
	}


	

	
	const copyToClpb  =  ()  => {

		copyToClipboard( value2copy ? value2copy: init,updObs);
	};
	

		
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updObs();
		}
	});

	if(!init){
		return null
	}

	return (
		<div onClick={copyToClpb}>
			<p className="label">
				{label}
			</p>
			<div  
				style={width? {width: width}: null}
				className="r7gAOb yyJm8b"
			>
				{init}
			</div>
		</div>
	)
}


const OpacityView  = ({ viewId, activeView, children }) => {
	return(
		<div className={"opacityView"} style={{opacity : viewId=== activeView?1:0, marginTop: 29 }}>
			{viewId=== activeView?children:null}
		</div>
	)

}